import { useParams } from 'react-router-dom';
import { ProductArr } from '../components/data';


function ViewProduct(props) {
    const params = useParams()
    const product_details = ProductArr[params.slug]

    console.log(params)
    return ( <>
    <div className="center_me" >

        <span>{product_details.name}</span>
        <span>{product_details.offer_price}</span>
        <span>{product_details.original_price}</span>
        <span>{product_details.details}</span>

    </div>
    </> );
}

export default ViewProduct;